<template>
<div>
	
	
<RateEditor :participantId="participantsId" readOnly='true'></RateEditor>


<div id="rate-card">

		<div id="rate-panel">
			<div class="content">
				<div class="weui-flex">
					<div class="weui-flex__item">
						<div class="content-flag" >
							<div class="logo active">完</div>
						</div>
					</div>
					<div class="weui-flex__item content-info"> </div>
					<div class="weui-flex__item content-info"> </div>
				</div>

			</div>
		</div>
</div>

	
	
</div>
</template>
<script>
	import RateEditor from './RateEditor';
	import server from '../assets/js/server.js'
	import router from'../router/index.js'
export default {
	data() {
      return {
        badge: this.$store.state.user.badge,
        rateLevel1: '',
        rateLevel2: '',
        raterId: '11111',
        title: '心得详情',
        participantsId: this.$route.query.id
      }
  	},
  	mounted() {
  		var this1 = this;
  		console.log(this.participantsId);
  		server.participantsViewDetail(this.participantsId).then(function(response) {
  		 	this1.rateLevel1 = response.data.rateLevel1;
  		 	this1.rateLevel2 = response.data.rateLevel2;
  		});
  		
  	},
  	methods: {

  	},
  	components: {
			RateEditor
		}
}
</script>
<style>
	.content .content-info {
	    width: 100%;
	    display: inline-block;
	    margin-top: 12px;
	    padding-right: 10px;
	    text-align: center;
	}
</style>


