<template>
	<div class="page__hd" style="padding-top:10px">

		<div v-show="this.readOnly == 'true'" class="search-bar"> <i class="weui-icon-search" style="width:5%; padding: 10px"></i>
			<input  style="width:150px" class="weui-input"  v-model="keyword" placeholder="关键字查询"/>
			<p class="counter">{{counter}}/{{result}}</p>
			<div class="searchButtonPre" @click="searchPre">↑</div>
			<div class="searchButtonNext" @click="searchNext">↓</div>
		</div>

		<div class="weui-loadmore" style="z-index:999"> <i class="weui-loading"></i>
			<span class="weui-loadmore__tips">正在加载</span>
		</div>

		<div v-bind:style="styleObject">

			<div v-show = "this.readOnly != 'true'" id="toolbar" >

				<select class="ql-align" style="display:none">
					<option selected></option>
				</select>
				<!-- Add a bold button -->
				<!-- <button class="ql-bold">Bold</button>
			-->
			<!--<button class="ql-italic">Italic</button>
		-->
		<button style="width: auto">字数：{{fontCount}}</button>
		<button @click="changeFormat" style="width: auto">【修正格式】</button>
<!-- 		段落字数：{{this.wordsbreak}} -->
		<!-- <button @click="changeFormat" style="width: auto">【缩进排版】</button>
	-->
	<!-- <button class="btn" style="width: auto" :data-clipboard-text="text">复制</button>
-->
</div>

<quill-editor id="my-quill-editor" v-bind:style="wechatStyle"
			v-model="content"
			:options="editorOption"
			@ready="onEditorReady($event)"
			@change="onEditorChange($event)"></quill-editor>

</div>

</div>
</template>
<script>
	import server from '../assets/js/server.js';
	import  '../assets/js/clipboard.min.js';

	var bindings = {
			// This will overwrite the default binding also named 'tab'
  			tab: {
    			key: 9,
    			handler: function(range, context) {    				
    				this.quill.insertText(range.index,'　');
					this.quill.setSelection(range.index + 1);
    			}
  			},
  			enter: {
  				key: 13,
    			handler: function(range, context) {
    				this.quill.insertText(range.index,'\n\n\　');
					this.quill.setSelection(range.index + 3);
    			}
  			},
	};

	export default {

		computed: {

			wechatStyle:function(){
				return '';
			},

  			styleObject:function() {
    			return {
    				padding: '0px ' + this.page_hd_padding + 'px',
    			}
  			},

			fontCountColor: function() {
				let right = 'right:' + (this.page_hd_padding + 8) + 'px;';
			    return (this.fontCount > 1200) ? right + 'color: red':  right +  'color:#484746'			    
			},

		},

		methods: {

			searchPre() {
				if(this.clickNext){
					this.starLocation -= 2;
					this.clickNext = false;
				}
				this.clickPre = true;
				let index = this.text.lastIndexOf(this.keyword,this.starLocation);
				let length = this.keyword.length;
				//console.log(index,this.starLocation,'1');
				if(index != -1) {
				
					this.editor.setSelection(index, length);
					this.starLocation = index -1;
					if(this.starLocation < 0) this.starLocation = this.text.length;
				}
				else {
					this.starLocation = this.text.length - 1;
					index = this.text.lastIndexOf(this.keyword,this.starLocation);
					this.editor.setSelection(index, length);
					this.starLocation = index - 1;
				}
				this.counter--;
				if(this.counter < 1) this.counter = this.result;
			},

			searchNext() {
				if(this.clickPre){
					this.starLocation += 2;
					this.clickPre = false;
				}
				this.clickNext = true;
				let index = this.text.indexOf(this.keyword,this.starLocation);
				let length = this.keyword.length;
				if(index != -1) {
					this.editor.setSelection(index, length);
					this.starLocation = index + 1;
				}
				else {
					this.starLocation = 0;
					index = this.text.indexOf(this.keyword,this.starLocation);
					this.editor.setSelection(index, length);
					this.starLocation = index + 1;
				}
				this.counter++;
				if(this.counter > this.result) this.counter = 1;
			},

			changeFormat(){
				this.editor.removeFormat(0 , this.content.length);
				this.content = this.textFormat(this.content);
			},

			textFormat(str){
				//str = str.replace(/\&nbsp;/g,"");
				//str = str.replace(/<br>/g,"");
				//str = str.replace(/<p><\/p>/g,"");				
				//str = str.replace(/<\/p><p>/g,"</p><br><p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;");
				//str = str.replace(/<p><\/p>/g,"<p><br></p>");
				//str = str.replace(/<br><\/p><p>/g,"<br></p><p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;");
				str = str.replace(/\s+/g,""); 
				str = str.replace(/\&nbsp;/g,"");
				str = str.replace(/<br>/g,"");
				str = str.replace(/<p><\/p>/g,"");	
				str = str.replace(/<p><br><\/p>/g,"");		
				str = str.replace(/<\/p><p>/g,"</p><br><p>&nbsp;&nbsp;&nbsp;&nbsp;");
				return str;
			},

			//word文字统计算法
			fontCountFont(str){
				let sLen = 0;
				try{
		        //先将回车换行符做特殊处理
		        str = str.replace(/(\r\n+|\s+|　+)/g,"龘");
		        //处理英文字符数字，连续字母、数字、英文符号视为一个单词
		        str = str.replace(/[\x00-\xff]/g,"m");  
		        //合并字符m，连续字母、数字、英文符号视为一个单词
		        str = str.replace(/m+/g,"*");
		        //去掉回车换行符
		        str = str.replace(/龘+/g,"");
		        //返回字数
		        sLen = str.length;
			    }catch(e){

			    }
			    return sLen;
			},

			onEditorReady(editor) {
				this.editor = editor;
			},

			onEditorChange(obj) {
				// obj.editor.removeFormat(0,this.content.length);
				//save to vuex
				this.$store.commit('saveWordscount',this.fontCount);

				this.fontCount = this.fontCountFont(obj.text);
				this.editor = obj.editor;
				this.text = obj.text;
				this.htmlcontent = obj.html;

				//console.log(this.htmlcontent);

				this.checkLines();
			},

			getByteLen(val) {
				var len = 0;
				
				for (var i = 0; i < val.length; i++) {
					var a = val.charAt(i);
					
					if (a.match(/[^\x00-\xff]/ig) != null) {
						len += 2;
					} else {
						len += 1;
					}
				}
				
				return len;
			},

			checkLines(){
			
				var strTemp = this.text.replace(/(\r\n+|\s+|　+)/g,"SPILTTAG");
				
				var strArray = strTemp.split("SPILTTAG");
				var ktemp = "";
				//210字符
				for (var i = 0; i < strArray.length - 1 ; i++) {
					//console.log(i +' ： ' + content.match(strArray[i]).length);
					var pLength = this.getByteLen(strArray[i]);
					if(pLength > 208){
						this.$store.commit('wordslineBreak',true);
						break;
					}else{
						this.$store.commit('wordslineBreak',false);
					}

					ktemp += pLength+' ';
				}

				this.wordsbreak = ktemp;
			}
		},

		watch: {
			keyword: function(val){
				
				//this.editor.removeFormat(0,this.content.length);
				
				var regExp = new RegExp(this.keyword, 'g');
				
				if (this.keyword.length > 0 && this.keyword != '.' && regExp.test(this.text)){
					this.counter = this.result = this.text.match(regExp).length;
					//this.counter = 	this.text.match(regExp).length;		
					this.content = this.htmlcontent.replace(regExp, "<span class='highlight' style='background: yellow; color: red;'>"+this.keyword+'</span>');
					this.starLocation = 0;
					this.counter = 0;
					//this.editor.setSelection(this.text.indexOf(this.keyword,this.starLocation),0);
					//console.log(this.content);

				}
				else {
					this.counter = 0;
					this.result = 0;
					//console.log('无查询结果');
				}
			},

            screenWidth: function(val) {

                this.screenWidth = val;

                if(this.screenWidth >= 768){

                	this.page_hd_padding = 30;

                }else{
                	this.page_hd_padding = (this.screenWidth - 80 - 280)/2;
                }

                
            }
        },

		mounted() {

			console.log(this.participantId);

			new Clipboard('.btn');
			
			//set readonly			
			if(this.readOnly == 'true') {

				this.editor.enable(false);
				//搜索栏40+底栏50
	        	document.getElementsByClassName('ql-editor')[0].style.maxHeight = document.body.clientHeight - 100 + 'px';
			}
			else {
				this.editor.enable(true);
				//底栏50+工具栏43px
	        	document.getElementsByClassName('ql-editor')[0].style.maxHeight = document.body.clientHeight - 110 + 'px';
			}

			let this2 = this;

			//监听宽度 调整样式
			this.screenWidth = document.body.clientWidth;

            window.onresize = () => {
                return (() => {
                    this2.screenWidth = document.body.clientWidth;
                })()
            }
            //监听复制事件
            //document.getElementById("my-quill-editor").oncopy = this.changeFormat;

			server.participantsView(this.participantId).then(function(response){
				//console.log(response);
				this2.content = response.data.content;
	        	document.getElementsByClassName('weui-loadmore')[0].style.display = 'none';
	        }, function(response) {
	            //console.log(response);
	        });

		},

		props :['participantId', 'readOnly' ],

		data() {

			return {
				editor: null,
				content: '',
				keyword: '',
				text: '',
				htmlcontent: '',
				screenWidth: '',
				clickPre: false,
				clickNext: false,
				editorOption: {
					placeholder: " " ,
					modules:{
						toolbar: '#toolbar',
						//toolbar : toolbarOptions,
						keyboard: {
      						bindings: bindings
    					},
    				},

				},
				starLocation: 0,
				result: 0,
				counter: 0,
				fontCount: '',
				page_hd_padding : 0,
				wordsbreak:'',

			}
		}
	}
</script>
<style >

	.search-bar {
		background-color: #F4F3F1;
    	margin: 5px auto 0px auto;
    	width: 280px;
    	height: 35px;
	}

	.searchButtonPre{
		position: relative;
		top: -55px;
		height: 25px;
		width: 25px;
 		left: 225px;
 		text-align: center;
	}

	.searchButtonNext{
		position: relative;
		top: -80px;
		height: 25px;
		width: 25px;
 		left: 250px;
 		text-align: center;
	}

	.counter {
		position: relative;
		width: 40px;    	
		left: 190px;
    	top: -27px;
    	color: #888;
    	font-size: 15px;
	}

	.fontCount {
		position: fixed;
		top: 40px;
		right: 10px;
		font-size: 12px;
		color: #484746;
		text-align: right;
	}

	.ql-toolbar.ql-snow{
		border-bottom: 0px;
	}

	.page__hd {
		padding: 0px 40px 0px 40px;
	}

</style>


